<template>
    <div class="container">
        <div id="settings">
        
            <transition name="modal">
                <div class="modal fade show" tabindex="-1" role="dialog">
                    <div class="modal-dialog">
                        <div class="modal-content">
                            <div class="modal-header">
                                <h5 class="modal-title">{{ activeLang.settings }}</h5>
                            </div>
                            <div class="modal-body">
                                
                                <label for="select-language">{{ activeLang.language }}</label>
                                <select 
                                    id="select-language"
                                    class="custom-select"
                                    v-model="selectedLanguage"
                                    v-on:change="setActiveLang()">
                                    <option value="rus">{{ activeLang.russian }}</option>
                                    <option value="eng">{{ activeLang.english }}</option>
                                </select>
                                
                                <label for="select-difficulty" class="mt-3">{{ activeLang.difficulty }} (<span class="text-danger">*{{ activeLang.difficulty_text }}</span>)</label>
                                <select 
                                    id="select-difficulty"
                                    class="custom-select"
                                    v-model="selectedDifficulty"
                                    v-on:change="setActiveDifficulty()">
                                    <option value="easy">{{ activeLang.easy }}</option>
                                    <option value="medium">{{ activeLang.medium }}</option>
                                    <option value="hard">{{ activeLang.hard }}</option>
                                </select>

                            </div>
                            <div class="modal-footer">
                                <button 
                                    type="button" 
                                    class="btn btn-secondary" 
                                    data-dismiss="modal"
                                    v-on:click="setDeactivateSettings()"
                                >
                                    {{ activeLang.close }}
                                </button>
                            </div>
                        </div>
                    </div>
                </div>
            </transition>

            <transition name="fade">
                <div class="modal-backdrop fade show"></div>
            </transition>

        </div>
    </div>
</template>

<script>
export default {
    name: 'Settings',
    
    data () {
      return {
          selectedLanguage: 'rus',
          selectedDifficulty: 'easy'
      }
    },

    props: {
        activeLang: Object,
        activeEasy: Boolean,
        activeMedium: Boolean,
        activeHard: Boolean
    },

    methods: {
        setDeactivateSettings () {
            this.$emit('set-deactivate-settings');
        },

        setActiveLang () {
            if (this.selectedLanguage === 'eng') {
                this.$emit('set-lang-eng');
            }
            else {
                this.$emit('set-lang-rus');
            }
        },

        setActiveDifficulty () {
            if (this.selectedDifficulty === 'medium') {
                this.$emit('set-difficulty-medium');
            }
            else if (this.selectedDifficulty === 'hard') {
                this.$emit('set-difficulty-hard');
            }
            else {
                this.$emit('set-difficulty-easy');
            }

            this.setActiveGame();
            
        },

        setActiveGame () {
            this.$emit('set-active-game');
        }
    },

    created () {
        if (this.activeLang.russian === 'Russian') {
            this.selectedLanguage = 'eng'
        }
        else {
            this.selectedLanguage = 'rus'
        }

        if (this.activeMedium) {
            this.selectedDifficulty = 'medium'
        }
        else if (this.activeHard) {
            this.selectedDifficulty = 'hard'
        }
        else {
            this.selectedDifficulty = 'easy'
        }
    }
}
</script>

