<template>

  <div id="app">
    
    <Header 
      v-on:set-active-main="setActiveMain()"
      v-on:set-active-game="setActiveGame()"
      v-on:set-active-settings="setActiveSettings()"
      v-on:set-active-about="setActiveAbout()"

      v-bind:activeMain="activeMain"
      v-bind:activeLang="activeLang"
    />

    <Main 
      v-if="activeMain"

      v-bind:activeLang="activeLang"
    />

    <Game 
      v-if="activeGame"

      v-on:set-active-game="setActiveGame()"

      v-bind:activeLang="activeLang"
      v-bind:activeGame="activeGame"
      v-bind:activeEasy="activeEasy"
      v-bind:activeMedium="activeMedium"
      v-bind:activeHard="activeHard"
    />
    
    <Settings 
      v-if="activeSettings"

      v-on:set-deactivate-settings="setDeactivateSettings()"
      v-on:set-lang-eng="setLangEng()"
      v-on:set-lang-rus="setLangRus()"
      v-on:set-difficulty-medium="setDifficultyMedium()"
      v-on:set-difficulty-hard="setDifficultyHard()"
      v-on:set-difficulty-easy="setDifficultyEasy()"
      v-on:set-active-game="setActiveGame()"

      v-bind:activeLang="activeLang"
      v-bind:activeEasy="activeEasy"
      v-bind:activeMedium="activeMedium"
      v-bind:activeHard="activeHard"
    />
    
    <About 
      v-if="activeAbout"

      v-on:set-deactivate-about="setDeactivateAbout()"

      v-bind:activeLang="activeLang"
    />

    <Footer />

  </div>

</template>

<script>

import Header from './components/Header'
import Main from './components/Main'
import Game from './components/Game'
import Settings from './components/Settings'
import About from './components/About'
import Footer from './components/Footer'

import Lang from './assets/lang.json';

export default {
  name: 'App',

  data () {
    return {
      Lang,
      activeLang: Lang.rus,
      activeMain: true,
      activeGame: false,
      activeSettings: false,
      activeAbout: false,
      activeEasy: true,
      activeMedium: false,
      activeHard: false
    }
  },

  components: {
    Header, Main, Game, Settings, About, Footer
  },

  methods: {
        setActiveMain () {
            this.activeMain = true;
            this.activeGame = false;
            this.activeSettings = false;
            this.activeAbout = false;
        },

        setActiveGame () {
            this.activeMain = false;

            this.activeGame = false;
            setTimeout(
              () => {
                this.activeGame = true;
              }, 1
            );

            this.activeSettings = false;
            this.activeAbout = false;
        },

        setActiveSettings () {
            this.activeSettings = true;
            this.activeAbout = false;
        },

        setDeactivateSettings () {
            this.activeSettings = false;
        },

        setActiveAbout () {
            this.activeSettings = false;
            this.activeAbout = true;
        },

        setDeactivateAbout () {
            this.activeAbout = false;
        },

        setLangEng () {
            this.activeLang = Lang.eng;
        },

        setLangRus () {
            this.activeLang = Lang.rus;
        },

        setDifficultyMedium () {
          this.activeMedium = true;
          this.activeHard = false;
          this.activeEasy = false;
        },

        setDifficultyHard () {
          this.activeMedium = false;
          this.activeHard = true;
          this.activeEasy = false;
        },

        setDifficultyEasy () {
          this.activeMedium = false;
          this.activeHard = false;
          this.activeEasy = true;
        }
    }
}

</script>

<style>

* {
  margin: 0;
  padding: 0;  
  outline: none;
  border: none;
  box-sizing: border-box;
}

body {
  background: #8989ba;
}

h5,
.modal-footer {
    margin: 0 auto;
}

.modal {
    display: block;
}

/* Animations */

.modal-enter-active, 
.modal-leave-active {
    transition: all .7s;
}

.modal-enter, 
.modal-leave-to {
    transform: translateY(-360px);
}

.fade-enter-active {
    animation: .75s fade forwards;
}

.fade-leave-active {
    animation: .75s fade reverse;
}

.fade-enter, 
.fade-leave-to {
    opacity: 0;
}

@keyframes fade {
    from {
        opacity: 0;
    }
    to {
        opacity: .5;
    }
}
</style>
