<template>
    <header>
        <button 
            class="btn btn-success" 
            v-on:click="setActiveGame()">
            {{ activeLang.new_game }}
        </button>
        <button 
            class="btn btn-success" 
            v-on:click="setActiveSettings()">
            {{ activeLang.settings }}
        </button>
        <button 
            class="btn btn-success" 
            v-on:click="setActiveAbout()">
            {{ activeLang.about }}
        </button>
        <button 
            class="btn btn-success" 
            v-on:click="setActiveMain()" 
            v-if="!activeMain">
            {{ activeLang.exit }}
        </button>
    </header>
</template>

<script>

export default {
    name: 'Header',
    
    data () {
      return {
      }
    },

    props: {
        activeMain: {
            type: Boolean
        },

        activeLang: Object
    },

    methods: {
        setActiveMain () {
            this.$emit('set-active-main');
        },

        setActiveGame () {
            this.$emit('set-active-game');
        },

        setActiveSettings () {
            this.$emit('set-active-settings');
        },
        
        setActiveAbout () {
            this.$emit('set-active-about');
        }
    }
}
</script>

<style scoped>
header {
    display: flex;
    justify-content: center;
    padding-top: 5px;
    background-image: linear-gradient(to top, #8989ba 0%, #8989ba 52%, #a7a6cb 100%);
}

button {
    margin-left: 5px;
    margin-right: 5px;
}
</style>