<template>

    <div id="game">

        <div class="container">
            <div class="row row-cols-4">
                <div 
                    class="card"
                    v-for="(number, index) in arrCards"
                    v-bind:key="index"
                    v-on:click="clickToCard(index)">
                </div>
            </div>
        </div>

        <div class="container statistics">
            <span class="badge badge-pill badge-success mr-5">{{ activeLang.difficulty }}: {{ activeEasy?activeLang.easy:activeMedium?activeLang.medium:activeLang.hard}}</span>
        </div>

        <div class="container statistics">
            <span class="badge badge-pill badge-success mr-3" v-if="this.activeHard">{{ activeLang.amount_open }}: {{ amountOpen }}</span>
            <span class="badge badge-pill badge-success mr-3">{{ activeLang.amount_step }}: {{ amountStep }}</span>
            <span class="badge badge-pill badge-success">{{ activeLang.time }}: <span v-if="amountMinutes >= 1">{{ amountMinutes }} {{ activeLang.min }}.</span> {{ amountSeconds }} {{ activeLang.sec }}.</span>
        </div>
        
        <transition name="modal">
            <div class="modal fade show" tabindex="-1" role="dialog" v-if="isWin">
                <div class="modal-dialog">
                    <div class="modal-content">
                        <div class="modal-header">
                            <h5 class="modal-title">{{ activeLang.you_win }}!!!</h5>
                        </div>
                        <div class="modal-body">
                            <p>{{ activeLang.difficulty }}: {{ activeEasy?activeLang.easy:activeMedium?activeLang.medium:activeLang.hard}}</p>
                            <p>{{ activeLang.amount_step }}: {{ amountStep }}</p>
                            <p>{{ activeLang.spent_time }}: <span v-if="amountMinutes >= 1">{{ amountMinutes }} {{ activeLang.min }}.</span> {{ amountSeconds }} {{ activeLang.sec }}.</p>
                            <p>{{ activeLang.again }}?</p>
                        </div>
                        <div class="modal-footer">
                            <button 
                                type="button" 
                                class="btn btn-primary"
                                v-on:click="setActiveGame()"
                            >
                                {{ activeLang.new_game }}
                            </button>

                            <button 
                                type="button" 
                                class="btn btn-secondary" 
                                data-dismiss="modal"
                                v-on:click="isWin = false"
                            >
                                {{ activeLang.close }}
                            </button>
                        </div>
                    </div>
                </div>
            </div>
        </transition>

        <transition name="fade">
            <div class="modal-backdrop fade show" v-if="isWin"></div>
        </transition>

    </div>

</template>

<script>

export default {
    name: 'Game',

    data () {
      return {
          arrCards: [],
          arrCardsOpen: [],
          amountCards: 16,
          amountOpen: 0,
          amountStep: 0,
          index_a: 0,
          index_b: 0,
          statusGame: 0,
          intervalId: '',
          amountSeconds: 0,
          amountMinutes: 0,
          isWin: false
      }
    },

    props: {
        activeLang: Object,
        activeGame: Boolean,
        activeEasy: Boolean,
        activeMedium: Boolean,
        activeHard: Boolean
    },
    
    methods: {
        clickToCard (index) {
            if (!this.activeHard && this.arrCardsOpen[index]) return;
            
            switch (this.statusGame) {
                case 0:
                    this.playStatusGame_0(index);
                    break;
                case 1:
                    this.playStatusGame_1(index);
                    break;
                case 2:
                    this.playStatusGame_2(index);
                    break;
            }
        },

        playStatusGame_0 (index) {
            this.index_a = index;
            this.showCard(index);
            this.statusGame = 1;
        },

        playStatusGame_1 (index) {
            this.index_b = index;
            if (this.index_a === this.index_b) return;
            this.showCard(index);

            this.amountStep++;

            if (this.arrCards[this.index_a] === this.arrCards[this.index_b]) {
                
                if (this.activeHard && !this.arrCardsOpen[this.index_a] && !this.arrCardsOpen[this.index_b]) {
                    this.amountOpen += 2;
                }
                else if (this.activeEasy || this.activeMedium) {
                    this.amountOpen += 2;
                }
                

                this.arrCardsOpen[this.index_a] = true;
                this.arrCardsOpen[this.index_b] = true;

                if (this.amountOpen === 16) {
                    this.isWin = true;
                }
                else {
                    this.statusGame = 0;

                    if (this.activeHard) {
                        setTimeout (
                            () => {
                                this.hideCard(this.index_a);
                                this.hideCard(this.index_b);
                            }, 500
                        );
                    }
                }
            }
            else {
                this.statusGame = 2;
                let cards = document.querySelectorAll('div.card');
                cards[this.index_a].style.cursor = "pointer";
                cards[this.index_b].style.cursor = "pointer";

                if (this.activeMedium || this.activeHard) {
                    setTimeout (
                        () => {
                            this.hideCard(this.index_a);
                            this.hideCard(this.index_b);
                        }, 500
                    );
                }
            }
        },

        playStatusGame_2 (index) {
            this.hideCard(this.index_a);
            this.hideCard(this.index_b);
            this.playStatusGame_0(index);
        },

        shuffleCards () {
            let a = Math.floor(Math.random() * this.amountCards);
            let b = Math.floor(Math.random() * this.amountCards);
            if (a == b)
            {
                return;
            }
            let x = this.arrCards[a];
            this.arrCards[a] = this.arrCards[b];
            this.arrCards[b] = x;
        },

        showCard (index) {
            let cards = document.querySelectorAll('div.card');
            cards[index].textContent = this.arrCards[index];
            cards[index].style.cursor = "not-allowed";
        },

        hideCard (index) {
            let cards = document.querySelectorAll('div.card');
            cards[index].textContent = '';
            cards[index].style.cursor = "pointer";
        },

        setActiveGame () {
            this.isWin = false;
            this.$emit('set-active-game');
        },

        startTime () {
            
          this.intervalId = setInterval(
              () => {
                this.amountSeconds++;
              }, 1000
            );
        },

        endTime () {
            clearInterval(this.intervalId);
        }
    },

    created () {

        for (let i = 0; i < this.amountCards; i++) {
            this.arrCards[i] = i % (this.amountCards / 2) + 1;
            this.arrCardsOpen.push(false);
        }
        for (let i = 0; i < 100; i++) {
            this.shuffleCards();
        }

        if (this.activeGame) {
            this.startTime();
        }
    },

    updated () {
        if (this.isWin) {
            this.endTime();
        }

        if (this.amountSeconds === 60) {
            this.amountMinutes++;
            this.amountSeconds = 0;
        }
    }
}
</script>

<style scoped>

.container {
    margin-top: 20px;
}

.card {
    height: 150px;
    cursor: pointer;
    font-size: xx-large;
    text-align: center;
    padding-top: 45px;
    border: 5px solid #8989ba;
} 

.statistics,
.modal-body {
    text-align: center;
}

.statistics span {
    background-color: #28a745;
    font-size: 1rem;
    font-weight: 400;
}

</style>