<template>
    <div class="container">
        <div id="about">
        
            <transition name="modal">
                <div class="modal fade show" tabindex="-1" role="dialog">
                    <div class="modal-dialog">
                        <div class="modal-content">
                            <div class="modal-header">
                                <h5 class="modal-title">{{ activeLang.about }}</h5>
                            </div>
                            <div class="modal-body">
                                <p>{{ activeLang.some_information }}</p>
                            </div>
                            <div class="modal-footer">
                                <button 
                                    type="button" 
                                    class="btn btn-secondary" 
                                    data-dismiss="modal"
                                    v-on:click="setDeactivateAbout()"
                                >
                                    {{ activeLang.close }}
                                </button>
                            </div>
                        </div>
                    </div>
                </div>
            </transition>

            <transition name="fade">
                <div class="modal-backdrop fade show"></div>
            </transition>
        
        </div>
    </div>
</template>

<script>
export default {
    name: 'About',
    
    data () {
      return {
      }
    },

    props: {
        activeLang: Object
    },

    methods: {
        setDeactivateAbout () {
            this.$emit('set-deactivate-about');
        }
    }
}
</script>

